import React, { Component } from 'react';
import Skill from './Skill';

class Skills extends Component {
  constructor(props){
    super(props);
    this.state = {
      skills: [
        {
          name: 'javascript',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'node',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'react and redux',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'angular',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'vuejs with vuex',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'vuejs with vuex',
          level: 'expert',
          experience: 3,
          src: ''
        },
        {
          name: 'vuejs with vuex',
          level: 'expert',
          experience: 3,
          src: ''
        }
      ]
    };
    this.loadSkills = this.loadSkills.bind(this);
  }

  componentDidMount() {
    // this.writeBio();
  }

  loadSkills() {
    const { skills } = this.state;
    return skills.map((skill) => {
      return (<Skill skill={skill} />);
    });
  }

  render() {
    return (
      <div className="profile">
        <span className="heading">Skills</span>
        <div className="text">
          {
            this.loadSkills()
          }
        </div>
      </div>
    );
  }
}

export default Skills;
