import React from 'react';

const Skill = (props) => {
  const { skill } = props;
  return (
    <div className="skill">
      <img src={skill.src} alt={skill.name} className="img" />
      <div className="details">
        <span>{skill.name}</span>
        <span>{skill.experience}</span>
      </div>
    </div>
  );
};

export default Skill;
